import React, { useEffect, useState } from 'react';
import { useLocation, useHistory } from 'react-router-dom';
import * as api from '../../utils/MainApi';
import Header from '../Header/Header';
import './User.css';
import telImg from '../../assets/img/tel-img.svg';
import emailImg from '../../assets/img/email-img.svg';

function User({ logout, onEditAvatar }) {
  const history = useHistory();
  const { pathname } = useLocation();

  const [user, setUser] = useState({});

  // const currentUrl = document.location.pathname.slice(6);

  useEffect(() => {
    window.scrollTo(0, 0);
    api
      .getUser(document.location.pathname.slice(6))
      .then((profileInfo) => {
        setUser(profileInfo);
      })
      .catch((err) => {
        console.log(err);
        history.push('/not-found');
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathname]);

  return (
    <>
      <Header logout={logout} card={user} onEditAvatar={onEditAvatar} />
      <section className="user">
        <p className="user__description">
                    
          ,   ,   , ,  
          -.         , 
                  ,  
            .
          <br />
          <br />
                     
           .       : "   
              ,         
          ,   ,        
          ,    ".
          <br />
          <br />
                ,   
           .      
            ,     ,   
           -.
        </p>
        <ul className="user__contacts">
          <li className="user__contacts-item">
            <img className="user__tel-img" src={telImg} alt=" " />
            +7 (954) 333-44-55
          </li>
          <li className="user__contacts-item">
            <img className="user__email-img" src={emailImg} alt=" " />
            {user.email}
          </li>
        </ul>
      </section>
    </>
  );
}

export default User;
